<template>
    <div>
        <b-card 
        v-if="cleanData" v-for="data in cleanData" :key="data.index">
            <b-card-text>
                 <h3>{{data.movie_data}}</h3>
                {{data.date_time}}

                <h4>{{data.cinema_data}}</h4>
                <h5>Sediste {{data.seat_id}}</h5>
            </b-card-text>
            <template #footer>
                <p><b>$15.00</b></p>
                <!-- <b-button variant="success" @click="makePayment(data.index)">Pay</b-button> -->
            </template>
        </b-card>

        <b-alert v-if="payment_done" show variant="success">
            <h4 class="alert-heading">Whippie! You have successfully purchased your tickets!</h4>
            <hr>
            <p class="mb-0">
                <b-button @click="closeMessage">Okay!</b-button>
            </p>
        </b-alert>

        <!-- <b-button @click="izvlacenjePodataka">Pull</b-button> -->
        <span><b>Total amount ${{this.totalAmnt}}</b></span>
        <br />
        <b-button @click="platiSve" variant="warning">Pay all</b-button>
    </div>
</template>

<script>

import {mapState, mapActions} from 'vuex';

export default{
    name: 'CartView',
    data(){
        return{
            cleanData:null,
            totalAmnt: 0,
            payment_done: false
        }
    },

    computed: {
        ...mapState([
            'reservedSeats'
        ])
    },

    methods:{
        ...mapActions([
            'resetReserved'
        ]),
        async salaInfo(auditoriumid){
            return new Promise((resolve, reject) => {
                fetch(`http://localhost:8000/admin/auditorium/${auditoriumid}`)
                .then(res=>res.json())
                .then(resp=>{
                    // console.log(resp);
                    // return JSON.stringify(resp);
                    resolve(resp);
                });
            });
        },
        async scheduleInfo(schid){
            return new Promise((resolve, reject) => {
                fetch(`http://localhost:8000/admin/schedule/${schid}`)
                .then(res=>res.json())
                .then(resp=>{
                    // console.log(resp);
                    // return JSON.stringify(resp);
                    resolve(resp);
                })
            });
        },
        async movieInfo(movieid){
            return new Promise((resolve, reject) => {
                fetch(`http://localhost:8000/admin/movie/${movieid}`)
                .then(res=>res.json())
                .then(resp=>{
                    // console.log(resp);
                    // return JSON.stringify(resp);
                    resolve(resp);
                });
            });
        },
        async cinemaInfo(cinemaid){
            return new Promise((resolve, reject) => {
                fetch(`http://localhost:8000/admin/movietheater/${cinemaid}`)
                .then(res=>res.json())
                .then(resp=>{
                    // console.log(resp);
                    // return JSON.stringify(resp);
                    resolve(resp);
                });
            });
        },
        async izvlacenjePodataka(){
            // e.preventDefault();
            this.cleanData=[];
            for(let i=0;i<this.reservedSeats.length;i++){
                let sala = await this.salaInfo(this.reservedSeats[i].auditorium_res_id);
                let raspored = await this.scheduleInfo(this.reservedSeats[i].schedule_id);
                let film = await this.movieInfo(this.reservedSeats[i].movie_res_id);
                let bioskop = await this.cinemaInfo(sala.movietheater_id);
                console.log("Raspored ",raspored);
                console.log("Film", film);
                console.log("Bioskop", bioskop);
                console.log("Sala", sala);

                let ticket_obj = {
                    movie_data : film.tittle + " Zanr: " + film.genre,
                    date_time : "Datum: " + raspored.date + " Vreme: " + raspored.start_time + " - " + raspored.end_time,
                    cinema_data: bioskop.name,
                    index: i,
                    seat_id: this.reservedSeats[i].seat_res_id
                };
                
                this.cleanData.push(ticket_obj);
            }
        },

        makePayment(index){
            let obj = this.reservedSeats[index];
            fetch(`http://localhost:8000/admin/reservation/findRes/${obj.auditorium_res_id}/${obj.schedule_id}/${obj.seat_res_id}/${obj.movie_res_id}/${obj.user_res_id}`)
            .then(resp => resp.json())
            .then(reservation => {
                console.log(reservation);
                let res_id = reservation[0].id;

                let payment_obj = {
                    user_pay_id: obj.user_res_id,
                    seat_pay_id: obj.seat_res_id,
                    auditorium_pay_id: obj.auditorium_res_id,
                    movie_pay_id: obj.movie_res_id,
                    sch_pay_id: obj.schedule_id,
                    res_pay_id: res_id,
                    amount: 15
                };

                let http_body = JSON.stringify(payment_obj);

                console.log("Za placanje ", payment_obj);

                fetch(`http://localhost:8000/admin/payment/addPayment`, {
                    method: 'POST',
                    headers: {
                        'Content-Type' : 'application/json'
                    },
                    body: http_body
                })
                .then(res => res.json())
                .then(odg => {
                    if(odg.msg){
                        alert(odg.msg);
                    }

                    if(odg.err){
                        alert(odg.err);
                    }

                    console.log("Payment created ", odg);
                });
            });
            console.log("Za placanje -> ",this.reservedSeats[index]);
        },

        platiSve(e){
            e.preventDefault();
            for(let i=0;i<this.reservedSeats.length;i++){
                this.makePayment(i);
            }

            this.payment_done = true;
        },

        closeMessage(){
            this.payment_done = false;
            this.cleanData = null;
            this.resetReserved();
            this.$router.push({name:'home'});
        }
    },

    mounted(){
        this.izvlacenjePodataka();
    },

    watch:{
        cleanData(){
            this.totalAmnt = 0;
            console.log(this.cleanData.length);
            for(let i=0;i<this.cleanData.length;i++){
                this.totalAmnt += 15;
                // this.makePayment(i);
            }
        }
    }
}

</script>

<style scoped>
</style>