<template>
    <div class="sala">
        <header>
            <h2>{{"Sala broj " + this.sala.id}}</h2>
        </header>
        <!--items je raspored filmova-->

        <b-table v-if="items" id="rasporedTable" hover fixed small :fields="fields" :items="items" @row-clicked="rowClicked" striped>

        </b-table>
        
    </div>
</template>

<script>

    export default {
       name: "Sala",

       props: {
        sala: Object
       },
       
       data(){
         return{
            fields:["start_time", "end_time","date","tittle"],
            items: null
         }
       },

       mounted(){
        this.items = [];
        console.log("Sala render", this.sala);
//ovo mi je raspored sa filmovima to je items
        fetch(`http://localhost:8000/admin/schedule/findScheduleByAuditorium/${this.sala.id}`, {method: "GET"})
        .then(resp => resp.json())
        .then(res => {
            console.log("ca ", res);
            for(let i=0;i<res.length;i++){
                fetch(`http://localhost:8000/admin/movie/${res[i].movie_sch_id}`, {method:"GET"})
                .then(rez => rez.json())
                .then(re => {
                    console.log("Movie from schedule ", re);
                    let obj = {};
                    res[i]["movieID"] = re.id;
                    res[i]["tittle"] = re.tittle;

                    console.log(res);
                    this.items.push(res[i]);
                });
            }
        });
       },

       methods : {
        //matrica [i,j]
        rowClicked(record, index){
            // alert(record.tittle);
            this.$router.push({name:'reserve', params: {auditoriumid: record.auditorium_sch_id, scheduleid: record.id}});
        }
       }
    }
</script>

<style scoped>
 header{
    background-color: #333;
    color:white;
    padding: 1rem;
    text-align: center;
  }
</style>