<template>
    <div class="seat">
        <b-button @click="rezervisi" variant="primary">
            <img src="https://cdn-icons-png.flaticon.com/512/24/24868.png"/>{{"Sediste " + this.seat.id }}
        </b-button>
    </div>

</template>
<script>
    import {mapActions, mapState} from 'vuex';
    import VueJwtDecode from 'vue-jwt-decode';

    export default{
        name: 'SingleSeat',
        props: {
            seat: Object
        },

        computed: {
            ...mapState([
                'token'
            ])
        },

        data() {
            return {
                btnClass: "red-color",
                selected: 0
            }
        },
        methods:{
            ...mapActions([
                'addPrice',
                'addSeatToList',
                'removeSeatFromList'
            ]),
//izvlaci user iz tokena i res id ostalo ima u seat
            async rezervisi(e){
                e.preventDefault();

                if(!this.token){
                    alert('Please login or create an account');
                    return;
                }
                

                //alert("Sediste broj " + this.seat.id);
                this.addPrice(10);

                let usrid = VueJwtDecode.decode(this.token).userId;
                
                console.log("Korisnicki id ", usrid);
                
                let movie_id = 0;

                await fetch(`http://localhost:8000/admin/schedule/${this.seat.schedule_id}`, {method: "GET"})
                .then(resp => resp.json())
                .then(res => {
                    console.log(res);
                    movie_id = res.movie_sch_id;
                });

                let obj = {
                    user_res_id: usrid,
                    seat_res_id: this.seat.id,
                    auditorium_res_id: this.seat.auditorium_seat_id,
                    movie_res_id: movie_id,
                    schedule_id: this.seat.schedule_id
                };

                console.log("Objekat za slanje na fetch ",obj);
                
                this.addSeatToList(obj);
                // await fetch("http://localhost:8000/admin/reservation/addReservation/", 
                //     {
                //         method: "POST",
                //         headers:{
                //             'Content-Type' : 'application/json',
                //         },
                //         body: JSON.stringify(obj)
                //     }
                // )
                // .then(res => res.json())
                // .then(resp => {
                //     console.log("Rezervacija je kreirana ", resp);
                // });
            },
        }
    }
</script>

<style>
img {
    max-width: 70px;
}

.selected {
    background-color: red!important;
}
</style>