<template>
  <div class="home">
    <header>
    <h1>Movie theaters</h1>
   </header>
   <section>
    <TheathersHolder class="thHolder" v-if="movieTheathers" :theatherObjects="movieTheathers"/>
  </section>
  </div>
</template>

<script>

import TheathersHolder from '@/components/TheathersHolder.vue'


export default {
  name: 'Home',
  components: {
    TheathersHolder
  },

  data() {
    return {
      movieTheathers: null
    }
  },

  mounted() {
    this.movieTheathers = [];

    fetch("http://localhost:8000/admin/movietheater", {method: "GET"})
    .then(resp => resp.json())
    .then(res => {
      for(let i=0;i<res.length;i++){
        fetch(`http://localhost:8000/admin/location/${res[i].location_id}`, {method:"GET"})
        .then(lok=>lok.json())
        .then(lokacija=>{
            res[i]["lokacija"] =lokacija.street_name+","+lokacija.city_name;
            this.movieTheathers.push(res[i]);
        });
      }

      console.log(this.movieTheathers);
    });

  }
}
</script>

<style scoped>
  .thHolder { 
    justify-content: center;
  }
  header{
    background-color: #333;
    color:white;
    padding: 1rem;
    text-align: center;
  }
  section{
    background-color: #333;
  }
  template{
    background-color: #333;
  }
</style>