import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  //globalne promenljive
  state: {
    ticketPrice:0,
    token:'',
    reservedSeats:[],
    saleArr:[],
    seats:[]
  },
  //menjaju state
  mutations: {
    addTicket(state,price){
      state.ticketPrice+=price;
    },

    setToken(state,token){
      state.token=token;
      localStorage.token=token;
    },
    removeToken(state){
      state.token='';
      localStorage.token='';
    },
    addSeat(state,seat){
      state.reservedSeats.push(seat);
    },
    removeSeat(state,seat){
      state.reservedSeats.splice(state.reservedSeats.indexOf(seat),1);
    },

    clearReservedSeats(state,seat){
      state.reservedSeats = [];
    },

    addToSale(state, obj){
      state.saleArr.push(obj);
    },
    
    resetSaleArr(state){
      state.saleArr = [];
    },

    setSeat(state, obj){
      state.seats = obj;
    },

    resetSeat(state){
      state.seats = [];
    }
   

  },
  //commmit poziva izvrsavanje mutacije
  actions: {
    setSeatAction({commit}, obj){
      commit('setSeat', obj);
    },
    clearSeatArr({commit}){
      commit('resetSeat');
    },
    addPrice({commit}, pr){
      commit('addTicket',pr);
    },

    register({commit}, obj){
      fetch('http://127.0.0.1:9000/register',{
        method:'POST',
        headers:{'Content-Type':'application/json'},
        body: JSON.stringify(obj)
      }).then(res=>res.json())
      .then(tkn=>{
        if(tkn.msg){
          alert(tkn.msg);
        }else{
          commit('setToken', tkn.token);
        }
      });
    },

    login({commit},obj){
      fetch('http://127.0.0.1:9000/login',{
        method: 'POST',
        headers: {'Content-Type' : 'application/json'},
        body: JSON.stringify(obj)
      }).then(res => res.json())
      .then(tkn => {
        if(tkn.msg){
          alert(tkn.msg);
        }else{
          commit('setToken', tkn.token);
        }
      });
    },

    addSeatToList({commit},seat){
      commit('addSeat',seat);
    },
    removeSeatFromList({commit},seat){
      commit('removeSeat',seat);
    },
    resetReserved({commit}){
      commit('clearReservedSeats');
    },

    fetchAuditoriumByCinema({commit},id){
      commit('resetSaleArr');
      fetch(`http://localhost:8000/admin/auditorium/findByCinema/${id}`, {method: "GET"})
      .then(res => res.json())
      .then(resp => {
        for(let i=0;i<resp.length;i++){
          commit('addToSale',resp[i]);
        }
      });
    },

    fetchAvailableSeats({commit}, obj){

          let audid = obj.audid;
          let schid = obj.schid;
          //  this.seats=[];
          console.log("Function called with ", audid, schid);
          commit('resetSeat');
          // let auditorium_id = this.$route.params.auditoriumid;
            // let schedule_id = this.$route.params.scheduleid;

            // console.log(auditorium_id);
            // console.log(schedule_id);

            let seatsForAud = [];
            let reservationsForSchAud = [];
            let goodSeats = [];
            let tmpList=[];

            fetch(`http://localhost:8000/admin/seat/findSeatByAuditorium/${audid}`,{method:"GET"})
            .then(res=>res.json())
            .then(resp=>{
                for(let i=0;i<resp.length;i++){
                    console.log("Prvi fetch ", resp[i]);
                    seatsForAud.push(resp[i]);
                    tmpList.push(resp[i]);
                }

                console.log(seatsForAud);

                fetch(`http://localhost:8000/admin/reservation/findBySchAud/${audid}/${schid}`,{method:"GET"})
                .then(rez=>rez.json())
                .then(rezervation=>{
                    if(rezervation.length == 0){
                      for(let i=0;i<seatsForAud.length;i++){
                            // console.log("Schedule id in reserve view -> ", this.$route.params.scheduleid);
                            seatsForAud[i]["schedule_id"] = schid;
                            console.log("Current seat -> ", seatsForAud[i]);
                      }
                      commit('setSeat',seatsForAud);
                    }else{
                      for(let i=0;i<rezervation.length;i++){
                          console.log("Drugi fetch ", rezervation[i]);
                          reservationsForSchAud.push(rezervation[i]);
                      }

                      console.log("Sedista za auditorijum ", seatsForAud);
                      console.log("Rezervisana sedista ", reservationsForSchAud);
                      
                      let indexesToRemove = [];

                      for(let i=0;i<seatsForAud.length;i++){
                          for(let j=0;j<reservationsForSchAud.length;j++){
                              console.log("Poredim ", reservationsForSchAud[j].seat_res_id, " i ", seatsForAud[i].id)
                              if(reservationsForSchAud[j].seat_res_id === seatsForAud[i].id){
                                  console.log("usao sam ovde");
                                  console.log("Poredim ", seatsForAud[i].id, " i ",reservationsForSchAud[j].seat_res_id, " i=",i)
                                  indexesToRemove.push(i);
                              }
                              // console.log("Schedule id in reserve view -> ", this.$route.params.scheduleid);
                              seatsForAud[i]["schedule_id"] = schid;
                              console.log("Current seat -> ", seatsForAud[i]);
                          }
                      }

                      console.log("Ispis -> ", seatsForAud);
                      console.log("Ispis -> ", indexesToRemove);

                      for(let i=0;i<indexesToRemove.length;i++){
                          if(indexesToRemove[i] == 0){
                              seatsForAud.splice(indexesToRemove[i],1);
                          }else{
                              seatsForAud.splice(indexesToRemove[i] - 1, 1);
                          }
                      }
                          
                      console.log("seats for aud ", seatsForAud)
                      console.log("Available seats: ", goodSeats);
                      // this.seats = seatsForAud;
                      commit('setSeat',seatsForAud);
                    }
                  });
            });
    }

  }
});